<template>
<div>
    <span class="header">用户信息详情</span>
    <userTable class="table" :tabledata="data" :loading="load"></userTable>
</div>
</template>
<script>
import userTable from './userTable.vue'
export default {
    name:'Message',
    components:{
        userTable
    },
    data(){
        return{
            data:[],
            load:true
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getUserMes({id:''}).then((res)=>{
                if(res.success){
                    this.load = false
                    this.data=res.dataList
                }else{
                    this.$message.error('查找失败,请检查网络问题')
                }
            })
        }
    }
}
</script>
<style lang="scss">
.table{
        margin-left: 200px;
        margin-top: 80px;
    }
.header{
    font-size: 30px;
    position: relative;
    top:50px;
    right: 85px;
}
</style>


